  /* border-style: solid; */
  /* background-color: rgba(255, 255, 255, 0.828); */
  margin-top: 50px;
  /* width: 75vw; */
  width: 80vw;
  margin: 0 auto;
  padding: 20px 10px;
}

.ratingsReviewsContent {
  display: flex;
  justify-content: space-between;
}

/* ----- RATING BREAKDOWN ----- */
/* ----- RATING BREAKDOWN ----- */
/* ----- RATING BREAKDOWN ----- */

.ratingBreakdown {
  /* border-style: solid; */
  /* justify-content: left; */
  /* margin-right: 2%; */
  /* width: 28%; */
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  align-items: flex-start;
  /* position: relative; */
}

.stars-container {
  display: flex;
  justify-content: center;
  gap: 10px;
  align-items: center;
}

/* -- BreakdownComp -- */

.actualBreakdown {
  margin-top: 10px;
}

.breakdownRating {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.breakdownRating:hover {
  background-color: rgba(142, 142, 142, 0.505);
  border-radius: 7px;
  cursor: pointer;
}

#breakdownLabel {
  /* margin-left: 10px;
  margin-right: 10px; */
  width: 50px;
}

#breakdownBar {
  margin-left: 5px;
  width: 225px;
  height: 10px;
}

progress::-webkit-progress-bar {
  background-color: rgb(228, 228, 228);
  border-radius: 7px;
}

progress::-webkit-progress-value {
  background-color: #007185;
  /* background-color: #1d1a4d; */
  border-radius: 7px;
  box-shadow: 1px 1px 3px 3px rgba(167, 167, 167, 0.392);
}

/* -- ProductBreakdown -- */

.productBreakdown {
  margin-top: 5%;
  /* margin-bottom: 2px; */
  width: 100%;
}

.productBreakdownIndividual {
  width: 100%;
}

.productBreakdownScale {
  margin-top: 2px;
  height: 5px;
  width: 100%;
  background-color: #d9d9d9;
}

/*
.pointer {
  position: relative;
}

.pointer::before {
  content: attr(value);
