.4em + (1em * 0.25) + 1em;\n  }\n  \n  // Typing indicator (when exists)\n  & .#{$prefix}-typing-indicator {\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    // !!!! TODO: set to 20px when not using fancy scroll\n    right: 15px; \n    background-color: $message-list-typing-indicator-bg-color;\n    padding: 0.5em 0.5em;\n    font-size: 0.9em;\n    height: 1em + (1em * 0.25);\n    line-height: 1em + (1em * 0.25);\n  }\n  \n  // Because message in group cant have max-width, only if its direct child of list\n  &__scroll-wrapper > .#{$prefix}-message,\n  &__scroll-wrapper > .#{$prefix}-message-group\n  {\n    max-width: 85%;\n  }\n\n  & .#{$prefix}-message.#{$prefix}-message--incoming,\n  & .#{$prefix}-message-group.#{$prefix}-message-group--incoming,\n  {\n    margin-right: auto;\n  }\n  \n  & .#{$prefix}-message.#{$prefix}-message--outgoing,\n  & .#{$prefix}-message-group.#{$prefix}-message-group--outgoing,\n  {\n    margin-left: auto;\n  }\n\n  & .#{$prefix}-message-separator:not(:first-child) {\n    margin-top: $message-list-message-separator-margin-top;\n  }\n\n  &__loading-more {\n    box-sizing: content-box;\n    @include flex-row;\n    justify-content: center;\n    position:absolute;\n    background-color: $message-list-loading-more-bg-color;\n    padding: $message-list-loading-more-padding;\n    top:0;\n    left:0;\n    right:0;\n    height: $message-list-loading-more-height;\n    z-index: 1;\n\n    .#{$prefix}-loader {\n      width:$message-list-loading-more-height;\n      height:$message-list-loading-more-height;\n    }\n    .#{$prefix}-loader::before,\n    .#{$prefix}-loader::after\n    {\n      width:100%;\n      height:100%;\n    }\n    \n    &--bottom {\n      top:initial;\n      bottom:0;\n    }\n    \n  }\n  \n  // Perfect scrollbar fix to hide loading more indicator\n  .ps__rail-y {\n    z-index:2;\n  }\n  \n}\n","@import \"../variables\";\n@import \"../helpers/mixins\";\n\n.#{$prefix}-avatar {\n  \n  $avatar-class: &;\n  \n  position: relative;\n  width: $avatar-md-width;\n  height: $avatar-md-width;\n  border-radius: 50%;\n  box-sizing: border-box;\n  \n  & > img {\n    box-sizing: border-box;\n    width: 100%;\n    height: 100%;\n    border-radius: 50%;\n  }\n\n  &:hover {\n    > img {\n      filter: brightness(115%);\n    }\n  }\n\n  &#{&}--xs {\n    width: $avatar-xs-width;\n    height: $avatar-xs-width;\n    min-width: $avatar-xs-width;;\n    min-height: $avatar-xs-width;\n  }\n\n  &#{&}--sm {\n    width: $avatar-sm-width;\n    height: $avatar-sm-width;\n    min-width: $avatar-sm-width;;\n    min-height: $avatar-sm-width;\n  }\n  \n  &#{&}--md\n  {\n    width: $avatar-md-width;\n    height: $avatar-md-width;\n    min-width: $avatar-md-width;;\n    min-height: $avatar-md-width;\n  }\n\n  &#{&}--lg {\n    width: $avatar-lg-width;\n    height: $avatar-lg-width;\n    min-width: $avatar-lg-width;;\n    min-height: $avatar-lg-width;\n  }\n  \n    &#{&}--fluid {\n    width: $avatar-fluid-width;\n    height: $avatar-fluid-width;\n    .#{$prefix}-status {\n      right: 5%;\n      width: 22%;\n      height: 22%\n    }\n  }\n  \n  // Status position and size\n  & .#{$prefix}-status {\n    box-sizing: border-box;\n    position: absolute;\n    right:-1px;\n    bottom: 3%;\n    \n    &__bullet  {\n      box-sizing: content-box;\n      border: solid 2px #fff;\n    }\n\n    $status-class: #{$prefix}-status;\n    $bullet-class: #{$prefix}-status__bullet;\n    \n    &--xs,\n    &--xs:not(.#{$status-class}--named) {\n      \n      @include status-size(\"xs\", 1);\n      right: 0;\n      bottom: 0;\n      \n    }\n\n    &--sm,\n    &--sm:not(.#{$status-class}--named) {\n      \n      @include status-size(\"sm\", 2);\n      \n      right: -3px;\n      bottom: -1px;\n    }\n    \n    &--md,\n    &--md:not(.#{$status-class}--named) {\n      @include status-size(\"md\", 2);\n    }\n\n    &--lg,  \n    &--lg:not(.#{$status-class}--named) {\n\n      @include status-size(\"lg\", 2);\n      bottom:4%;\n      \n    }\n    \n  }\n  \n  &#{$avatar-class}--active {\n    \n  }\n}\n","@import \"../variables\";\n\n$avatar-width-short-factor: 0.618;\n\n.#{$prefix}-avatar-group {\n  $class-name: &;\n  \n  @include flex-row-reverse;\n  overflow:visible;\n  position:relative;\n  flex-wrap: wrap;\n  box-sizing: border-box;\n  min-width: $avatar-group-md-min-width;\n  align-content: flex-start;\n  justify-content: flex-end;\n  \n  .#{$prefix}-avatar--active {\n    z-index:1;\n  }\n\n  .#{$prefix}-avatar--active-on-hover:hover {\n    z-index:2;\n  }\n  \n  &--xs {\n    \n    $xs-padding: $avatar-xs-width - $avatar-xs-width * $avatar-width-short-factor;\n    padding-left: $xs-padding;\n    padding-top: $xs-padding;\n    \n      .#{$prefix}-avatar {\n        margin-top: - $xs-padding;\n        margin-left:- $xs-padding;\n        width: $avatar-xs-width;\n        height: $avatar-xs-width;\n        min-width: $avatar-xs-width;\n        min-height: $avatar-xs-width;\n        border: none;\n        border-right: 1px solid rgb(242, 242, 242);\n      }\n    \n  }\n  \n  &--sm {\n    \n    $sm-padding: $avatar-sm-width - $avatar-sm-width * $avatar-width-short-factor;\n    padding-left: $sm-padding;\n    padding-top: $sm-padding;     \n    \n      .#{$prefix}-avatar {\n        margin-top: - $sm-padding;\n        margin-left:- $sm-padding;\n        width: $avatar-sm-width;\n        height: $avatar-sm-width;\n        min-width: $avatar-sm-width;\n        min-height: $avatar-sm-width;\n      }\n  }\n\n  &--md {\n    \n    $md-padding: $avatar-md-width - $avatar-md-width * $avatar-width-short-factor;\n    padding-left: $md-padding;\n    padding-top: $md-padding;\n    \n      .#{$prefix}-avatar {\n        margin-top: - $md-padding;\n        margin-left:- $md-padding;\n        width: $avatar-md-width;\n        height: $avatar-md-width;\n        min-width: $avatar-md-width;\n        min-height: $avatar-md-width;\n        border: 2px solid rgb(242, 242, 242);\n      }\n        \n  }\n  \n  &--lg {\n    \n    $lg-padding:  $avatar-lg-width - $avatar-lg-width * $avatar-width-short-factor;\n    padding-left: $lg-padding;\n    padding-top: $lg-padding;\n    \n    .#{$prefix}-avatar {\n      margin-top: - $lg-padding;\n      margin-left:- $lg-padding;\n      width: $avatar-lg-width;\n      height: $avatar-lg-width;\n      min-width: $avatar-lg-width;\n      min-height: $avatar-lg-width;\n      border: 2px solid rgb(242, 242, 242);\n    }\n    \n  }\n  \n}","@import \"../variables\";\n@import \"../helpers/mixins\";\n\n// Overflows\n$_message-input-overflow: visible;\n$_message-input-content-editor-overflow: visible;\n\n// Don to override this value (no !default)\n$_message-input-content-editor-border-vertical-width: $message-input-content-editor-border-top-width + $message-input-content-editor-border-bottom-width;\n// Don to override this value (no !default)\n$_message-input-content-editor-border-horizontal-width: $message-input-content-editor-border-left-width + $message-input-content-editor-border-right-width;\n\n// Calculated - do not override\n$_message-input-content-editor-padding-vertical: $message-input-content-editor-container-padding-top + $message-input-content-editor-container-padding-bottom;\n$_message-input-content-editor-padding-horizontal: $message-input-content-editor-container-padding-left + $message-input-content-editor-container-padding-right;\n\n// Calculated - do not override\n$_message-input-content-editor-container-min-height: $message-input-content-editor-container-line-height + $_message-input-content-editor-padding-vertical + $_message-input-content-editor-border-vertical-width;\n\n// Calculated - do not override\n$_message-input-content-editor-container-max-height: $message-input-max-lines * $message-input-content-editor-container-line-height + $_message-input-content-editor-padding-vertical + $_message-input-content-editor-border-vertical-width;\n\n.#{$prefix}-message-input {\n\n  // Local alias\n  $message-input-class: &;\n  \n  @include flex-row;\n  position: relative;\n  background-color: $message-input-bg-color;\n  border-top: $message-input-border-top;\n  border-right: $message-input-border-right;\n  border-bottom: $message-input-border-bottom;\n  border-left: $message-input-border-left;\n  box-sizing: border-box;\n  padding: $message-input-padding;\n  overflow: $_message-input-overflow;\n  flex-shrink: 0;\n\n  &__content-editor-wrapper {\n    box-sizing: border-box;  \n    position: relative;\n    background-color: $message-input-content-editor-wrapper-bg-color;\n    margin: $message-input-content-editor-wrapper-margin;\n    padding: $message-input-content-editor-wrapper-padding;\n    border-radius: $message-input-content-editor-wrapper-border-radius;\n    flex-grow:1;\n\n    #{$message-input-class}--disabled & {\n      background-color: $message-input-content-editor-disabled-bg-color;\n      color: $message-input-content-editor-disabled-color;\n    }\n    \n  }\n  \n  &__content-editor-container {\n    box-sizing: border-box;\n    position:relative;\n    background-color: $message-input-content-editor-container-bg-color;\n    @include flex-column;\n    align-items: stretch;\n    overflow:hidden;\n    overflow-y:auto;\n    font-size: $message-input-content-editor-container-font-size;\n    line-height: $message-input-content-editor-container-line-height;\n    min-height: $_message-input-content-editor-container-min-height;\n    max-height: $_message-input-content-editor-container-max-height;\n    padding: $message-input-content-editor-container-padding;\n    scroll-padding: $message-input-content-editor-container-padding-bottom;\n    \n    #{$message-input-class}--disabled & {\n      color: $message-input-content-editor-disabled-color;\n      background-color: transparent;\n    }\n  }\n  \n  &__content-editor {\n    box-sizing: border-box;\n    flex-grow:1;\n    background-color: $message-input-content-editor-bg-color;\n    font-family: $message-input-content-editor-font-family;\n    outline: 0;\n    border-top: $message-input-content-editor-border-top;\n    border-right: $message-input-content-editor-border-right;\n    border-bottom: $message-input-content-editor-border-bottom;\n    border-left: $message-input-content-editor-border-left;\n    overflow: $_message-input-content-editor-overflow;\n    color: $message-input-content-editor-color;\n    overflow-wrap: anywhere;\n    word-break: break-word;\n    #{$message-input-class}--disabled & {\n      background-color: transparent; // Because bg color is from parent and we are using alpha\n      color: $message-input-content-editor-disabled-color;\n    }\n    \n  }\n  \n  &__content-editor[data-placeholder]:empty:before {\n    box-sizing: border-box;\n    content: attr(data-placeholder);\n    color: $message-input-placeholder-color;\n    display: block;\n    cursor:text;\n  }\n  \n  &__tools {\n    @include flex-row;\n    box-sizing: border-box;\n    .#{$prefix}-button {\n      font-size:1.2em;\n      margin-top:0;\n      margin-bottom:0;\n    }\n    \n    .#{$prefix}-button--send {\n      align-self: flex-end;\n    }\n\n    .#{$prefix}-button--attachment {\n      align-self: flex-end;\n    }\n    \n  }\n\n  &__tools:first-child {\n    .#{$prefix}-button:first-child {\n      margin-left: 0;\n    }\n    .#{$prefix}-button:last-child {\n      margin-right: 0;\n    }\n  }\n\n  &__tools:last-child {\n    .#{$prefix}-button:first-child {\n      margin-left: 0;\n    }\n    .#{$prefix}-button:last-child {\n      margin-right: 0;\n    }\n  }\n  \n}\n","@import \"../variables\";\n@import \"../helpers/mixins\";\n\n.#{$prefix}-input-toolbox {\n  box-sizing: border-box;\n  @include flex-row;\n  justify-content: flex-end;\n  margin: $input-toolbox-margin;\n  padding: $input-toolbox-padding;\n\n  & .#{$prefix}-button {\n    @include reset-button;\n    margin: $input-toolbox-btn-margin;\n    font-size: $input-toolbox-btn-font-size;\n  }\n  \n}\n","@import \"../variables\";\n@import \"../helpers/mixins\";\n\n.#{$prefix}-chat-container {\n  box-sizing: border-box;\n  @include flex-column;\n  height: 100%;\n  min-width: $chat-container-min-width; // Required for ellipsis in child elements\n  color: $chat-container-color;\n  background-color: $chat-container-bg-color;\n  \n  & .#{$prefix}-message-input {\n    border-top: $chat-container-message-input-border-top;\n    border-right: $chat-container-message-input-border-right;\n    border-bottom: $chat-container-message-input-border-bottom;\n    border-left: $chat-container-message-input-border-left;\n    \n    //box-shadow: 0px -4px 5px -5px rgba(0, 0, 0, 0.38);\n    margin-top:auto; // Message input and following elements (input toolbox) will be placed at the bottom of the chat-container\n    padding: 0.3em 0 0.3em 0em;\n    color: $chat-container-message-input-color;\n    background-color: $chat-container-message-input-bg-color;\n    \n    & .#{$prefix}-message-input__content-editor-wrapper:first-child {\n      margin-left:0.8em;\n    }\n\n    & .#{$prefix}-message-input__content-editor-wrapper:last-child {\n      margin-right:0.8em;\n    }\n    \n  }\n\n  & .#{$prefix}-input-toolbox {\n    margin: $chat-container-input-toolbox-margin;\n    padding: $chat-container-input-toolbox-padding;\n    color: $chat-container-input-toolbox-color;\n    background-color: $chat-container-input-toolbox-bg-color;\n  }\n  \n}\n\n","@import \"../variables\";\n@import \"../helpers/mixins\";\n\n.#{$prefix}-typing-indicator {\n  box-sizing: content-box;\n  font-size: $typing-indicator-font-size;\n  font-family: $typing-indicator-font-family;\n  @include flex-row;\n  align-items: stretch;\n  \n  &__indicator {\n    box-sizing: inherit;\n    background-color: $typing-indicator-indicator-bg-color;\n    @include flex-row;\n    align-items: center;\n  }\n\n  &__dot {\n    box-sizing: inherit;\n    animation: #{$prefix}-typing-indicator__typing-animation 1.5s infinite ease-in-out;\n    border-radius: 100%;\n    display: inline-block;\n    height: $typing-indicator-dot-size;\n    width: $typing-indicator-dot-size;\n    background-color: $typing-indicator-dot-bg-color;\n  }\n\n  &__dot:not(:last-child) {\n    margin-right: $typing-indicator-dot-spacing;\n  }\n\n  &__dot:nth-child(1) {\n    box-sizing: inherit;\n  }\n  &__dot:nth-child(2) {\n    animation-delay: 300ms;\n  }\n  &__dot:nth-child(3) {\n    animation-delay: 400ms;\n  }\n\n  &__text {\n    box-sizing: inherit;\n    @include text-truncate;\n    color: $typing-indicator-text-color;\n    background-color: $typing-indicator-text-bg-color;\n    margin: $typing-indicator-text-margin;\n    user-select: none;\n  }\n\n  @keyframes #{$prefix}-typing-indicator__typing-animation {\n    0% {\n      transform: translateY(0px);\n    }\n    28% {\n      transform: translateY(-5px);\n    }\n    44% {\n      transform: translateY(0px);\n    }\n  }\n}\n","@import \"../variables\";\n@import \"../helpers/mixins\";\n\n.#{$prefix}-conversation-header {\n  \n  $class-name: &;\n  \n  box-sizing: border-box;\n  color: $conversation-header-color;\n  background-color: $conversation-header-bg-color;\n  font-family: $conversation-header-font-family;\n  font-size: $conversation-header-font-size;\n  flex-shrink: 0;\n  user-select: none;\n  //box-shadow: 0px 2px 5px -2px rgba(0,0,0,0.38);\n  \n  @include flex-row;\n  align-items: stretch;\n  border-top: $conversation-header-border-top;\n  border-right: $conversation-header-border-right;\n  border-bottom: $conversation-header-border-bottom;\n  border-left: $conversation-header-border-left;\n  padding: 0.6em 0.9em;\n\n  &__back {\n    box-sizing: border-box;\n    margin-right:0.5em;\n    @include flex-row;\n    align-items: center;\n    cursor: pointer;\n    order: 1;\n    \n    button {\n      font-size: 1.4em;\n      padding:0;\n    }\n  }\n  \n  &__avatar {\n    box-sizing: border-box;\n    width: $avatar-md-width;\n    height: $avatar-md-width;\n    margin-right: 1em;\n    order: 2;\n\n    > .#{$prefix}-avatar {\n      > .#{$prefix}-status {\n        > .#{$prefix}-status__bullet {\n          border-color: $conversation-header-bg-color;\n        }\n      }\n    }\n\n    .#{$prefix}-avatar-group {\n      padding-right:0 !important;\n    }\n  }\n\n  &__content {\n    box-sizing: border-box;\n    @include flex-column;\n    order: 3;\n    flex-grow: 2;\n    justify-content: center;\n    min-width: $conversation-header-content-min-width; // Required for ellipsis in child elements\n    \n    #{$class-name}__user-name {\n      box-sizing: border-box;\n      @include text-truncate;\n      font-weight: bold;\n      color: $conversation-header-user-name-color;\n      background-color: $conversation-header-user-name-bg-color;\n    }\n\n    #{$class-name}__info {\n      box-sizing: border-box;\n      @include text-truncate;\n      color: $conversation-header-info-color;\n      background-color: $conversation-header-info-bg-color;\n      font-weight: normal;\n      font-size: 0.9em;\n    }\n    \n  }\n  \n  &__actions {\n    box-sizing: border-box;\n    @include flex-row;\n    align-items: center;\n    color: $conversation-header-actions-color;\n    background-color: $conversation-header-actions-bg-color;\n    margin: $conversation-header-actions-margin;\n    order: 4;\n    .#{$prefix}-button:not(:first-child) {\n      margin-left:0.2em;\n    }\n    \n    .#{$prefix}-button {\n      padding:0;\n    }\n    \n    .#{$prefix}-button.#{$prefix}-button--arrow,\n    .#{$prefix}-button.#{$prefix}-button--info,\n    .#{$prefix}-button.#{$prefix}-button--voicecall,\n    .#{$prefix}-button.#{$prefix}-button--videocall,\n    .#{$prefix}-button.#{$prefix}-button--star,\n    .#{$prefix}-button.#{$prefix}-button--adduser,\n    .#{$prefix}-button.#{$prefix}-button--ellipsis,\n    .#{$prefix}-button.#{$prefix}-button--ellipsis\n    {\n      font-size: 1.4em;\n      color:$conversation-header-action-button-color;\n      background-color: $conversation-header-action-button-bg-color;\n    }\n    .#{$prefix}-button.#{$prefix}-button--ellipsis {\n      font-size: 1.3em;\n    }\n    \n  }\n  \n  \n}\n","@use \"sass:math\";\n@import \"../variables\";\n@import \"../helpers/mixins\";\n@import \"../helpers/functions\";\n\n// Positions\n$_conversation-unread-top: 0.3em;\n$_conversation-unread-right: 0.8em;\n\n.#{$prefix}-conversation {\n\n  $class-name: &;\n  \n  @include flex-row;\n  box-sizing: border-box;\n  position: relative;\n  font-family: $conversation-font-family;\n  padding:  math.div($conversation-unread-font-size,2) + $_conversation-unread-top 0.8em math.div($conversation-unread-font-size,2) + $_conversation-unread-top 0.8em;\n  cursor: pointer;\n  justify-content: flex-start;\n  color: $conversation-color;\n  background-color: $conversation-bg-color;\n  user-select: none;\n  border-top: $conversation-border-top;\n  border-right: $conversation-border-right;\n  border-bottom: $conversation-border-bottom;\n  border-left: $conversation-border-left;\n\n  &:hover {\n    background-color: $conversation-hover-bg-color;\n    \n    > .#{$prefix}-avatar {\n      > .#{$prefix}-status {\n        > .#{$prefix}-status__bullet {\n          border-color: $conversation-hover-bg-color;\n        }\n      }\n    }\n\n    // Reset avatar brightness on hover\n    & > .#{$prefix}-avatar > img {\n      filter: none;\n    }\n    \n  }\n  \n  &#{&}:active {\n    \n    color: $conversation-active-color;\n    background-color: $conversation-active-bg-color;\n    border-top: $conversation-active-border-top;\n    border-right: $conversation-active-border-right;\n    border-bottom: $conversation-active-border-bottom;\n    border-left: $conversation-active-border-left;\n    \n    > .#{$prefix}-avatar {\n      > .#{$prefix}-status {\n        > .#{$prefix}-status__bullet {\n          border-color: $conversation-active-bg-color;\n        }\n      }\n    }\n    \n  }\n\n  &#{&}--active {\n    \n    color: $conversation-name-activated-color;\n    background-color: $conversation-activated-bg-color;\n    font-weight: $conversation-activated-font-weigh