olor;\n    font-size: 0.8em;\n    @include text-truncate;\n  }\n\n  &__last-sender {\n    box-sizing: border-box;\n    display:inline;\n    font-weight: $conversation-last-sender-name-font-weight;\n  }\n\n  &__info-content {\n    box-sizing: border-box;\n    display:inline;\n    font-weight: $conversation-info-content-font-weight;\n  }\n\n  &__last-activity-time {\n    box-sizing: border-box;\n    margin: $conversation-last-activity-time-margin;\n    font-size: 0.8em;\n    @include flex-row;\n    align-items: center;\n  }\n  \n  &__last-activity-time + &__operations {\n    margin-left:0.5em;\n  }\n  \n  &__unread-dot {\n    box-sizing: content-box;\n    @include flex-row;\n    align-items: center;\n    margin-right: 0.5em;\n    align-self: center;\n\n    border-radius: 50%;\n    width: $conversation-unread-dot-width;\n    min-width: $conversation-unread-dot-width;\n    height: $conversation-unread-dot-width;\n    box-shadow: 1px 1px 1px 0px rgba(179, 179, 179, 1);\n    perspective: 200px;\n    perspective-origin: 50% 50%;\n\n    background: radial-gradient(\n                    circle at 3px 3px,\n                    lighten($conversation-unread-dot-color, 17),\n                    $conversation-unread-dot-color\n    );\n    \n  }\n\n  &__unread-dot {\n    \n    &:hover::before {\n      content: \"\";\n      position: absolute;\n      top: 1%;\n      left: 5%;\n      border-radius: 50%;\n      z-index: 2;\n\n      filter: blur(0);\n      height: 80%;\n      width: 40%;\n      background: radial-gradient(\n                      circle at 130% 130%,\n                      rgba(255, 255, 255, 0) 0,\n                      rgba(255, 255, 255, 0) 46%,\n                      rgba(255, 255, 255, 0.8) 50%,\n                      rgba(255, 255, 255, 0.8) 58%,\n                      rgba(255, 255, 255, 0) 60%,\n                      rgba(255, 255, 255, 0) 100%\n      );\n\n      transform: translateX(131%) translateY(58%) rotateZ(168deg) rotateX(10deg);\n    }\n    \n    &:hover::after {\n      content: \"\";\n      position: absolute;\n      top: 5%;\n      left: 10%;\n      width: 80%;\n      height: 80%;\n      border-radius: 100%;\n      filter: blur(1px);\n      z-index: 2;\n      transform: rotateZ(-30deg);\n\n      display: block;\n      background: radial-gradient(\n                      circle at 50% 80%,\n                      rgba(255, 255, 255, 0),\n                      rgba(255, 255, 255, 0) 74%,\n                      white 80%,\n                      white 84%,\n                      rgba(255, 255, 255, 0) 100%\n      );\n\n\n      animation: #{$prefix}-unread-anim 2s ease-out infinite;\n    }\n    \n  }\n  \n  // Without max width text will not be truncated\n  &__unread {\n    box-sizing: border-box;\n    position: absolute;\n    right: $_conversation-unread-right;\n    top: $_conversation-unread-top;\n    padding: 0.01em 0.3em;\n    border-radius: 0.3em;\n    font-size: $conversation-unread-font-size;\n    font-weight: 600;\n    color: $conversation-unread-color;\n    background-color: $conversation-unread-bg-color;\n    max-width: 40 * $conversation-unread-font-size;\n    @include text-truncate;\n    z-index:100; /* For grouped Avatar */\n    \n  }\n\n  &__unread:hover::before {\n    \n  }\n  \n  @keyframes #{$prefix}-unread-anim {\n    0% {\n      transform: scale(1);\n    }\n    20% {\n      transform: scaleY(0.95) scaleX(1.05);\n    }\n    48% {\n      transform: scaleY(1.1) scaleX(0.9);\n    }\n    68% {\n      transform: scaleY(0.98) scaleX(1.02);\n    }\n    80% {\n      transform: scaleY(1.02) scaleX(0.98);\n    }\n    97%,\n    100% {\n      transform: scale(1);\n    }\n  }\n  \n}\n","@import \"../variables\";\n@import \"../helpers/mixins\";\n\n/********** Colors **********/\n\n/********** Fonts **********/\n\n/********** Styles **********/\n.#{$prefix}-conversation-list {\n  box-sizing: border-box;\n  position: relative;\n  overflow: hidden;\n  height: 100%;\n  color: $conversation-list-color;\n  background-color: $conversation-list-bg-color;\n\n  & > div > ul {\n    @include reset-list;\n  }\n  \n  &__loading-more {\n    box-sizing: content-box;\n    @include flex-row;\n    justify-content: center;\n    position:absolute;\n    background-color: $conversation-list-loading-more-bg-color;\n    padding: $conversation-list-loading-more-padding;\n    bottom:0;\n    left:0;\n    right:0;\n    height: $conversation-list-loading-more-height;\n    z-index: 1;\n\n    .#{$prefix}-loader {\n      width:$conversation-list-loading-more-height;\n      height:$conversation-list-loading-more-height;\n    }\n    .#{$prefix}-loader::before,\n    .#{$prefix}-loader::after\n    {\n      width:100%;\n      height:100%;\n    }\n\n  }\n\n  // Perfect scrollbar fix to hide loading more indicator\n  .ps__rail-y {\n    z-index:2;\n  }\n  \n}\n","@import \"../variables\";\n@import \"../helpers/mixins\";\n\n.#{$prefix}-status {\n\n  box-sizing: border-box;\n  $status-class: &;\n  $bullet-class: #{$status-class}__bullet;\n  $name-class: #{$status-class}__name;\n  \n  position:relative;\n  @include flex-row;\n  align-items: center;\n  flex-wrap: nowrap;\n  font-size: $status-md-fontsize;\n  \n  #{$bullet-class} {\n    box-sizing: content-box;\n    width: $status-md-width;\n    height: $status-md-width;\n    border-radius: 50%;\n    position: relative;\n\n    // Bubble\n    perspective: 200px;\n    perspective-origin: 50% 50%;\n    // End Bubble\n    \n  }\n  \n  &--selected {\n    color: $status-selected-color;\n    background-color: $status-selected-bg-color;\n  }\n  \n  &--named {\n    width: auto;\n    height:auto;\n  }\n  \n  &__name {\n    margin: $status-name-margin;\n    line-height: 1.5;\n  }\n  \n  @at-root .#{$prefix}-avatar:hover #{$bullet-class}:before,\n  &:hover #{$bullet-class}:before {\n    box-sizing: border-box;\n    content: \"\";\n    position: absolute;\n    top: 1%;\n    left: 5%;\n    border-radius: 50%;\n    z-index: 2;\n\n    filter: blur(0);\n    height: 80%;\n    width: 40%;\n    background: radial-gradient(\n                    circle at 130% 130%,\n                    rgba(255, 255, 255, 0) 0,\n                    rgba(255, 255, 255, 0) 46%,\n                    rgba(255, 255, 255, 0.8) 50%,\n                    rgba(255, 255, 255, 0.8) 58%,\n                    rgba(255, 255, 255, 0) 60%,\n                    rgba(255, 255, 255, 0) 100%\n    );\n\n    transform: translateX(131%) translateY(58%) rotateZ(168deg) rotateX(10deg);\n  }\n  \n  @at-root .#{$prefix}-avatar:hover #{$bullet-class}:after, // For hover on parent avatar\n  &:hover #{$bullet-class}:after {\n    box-sizing: border-box;\n    content: \"\";\n    position: absolute;\n    top: 5%;\n    left: 10%;\n    width: 80%;\n    height: 80%;  \n    border-radius: 100%;\n    filter: blur(1px);\n    z-index: 2;\n    transform: rotateZ(-30deg);\n\n    display: block;\n    background: radial-gradient(\n                    circle at 50% 80%,\n                    rgba(255, 255, 255, 0),\n                    rgba(255, 255, 255, 0) 74%,\n                    white 80%,\n                    white 84%,\n                    rgba(255, 255, 255, 0) 100%\n    );\n    \n\n    animation: #{$prefix}-bubble-anim 2s ease-out infinite;\n  }\n  \n  \n  &--available {\n\n    #{$bullet-class} {\n      background: radial-gradient(\n                      circle at 3px 3px,\n                      lighten($status-available-color, 10),\n                      $status-available-color\n      );\n    }\n    \n    #{$name-class} {\n      color: $status-available-name-color;\n    }\n    \n  }\n     \n  &--unavailable {\n    #{$bullet-class} {\n      background: radial-gradient(\n                      circle at 3px 3px,\n                      lighten($status-unavailable-color, 25),\n                      $status-unavailable-color\n      );\n    }\n\n    #{$name-class} {\n      color: $status-unavailable-name-color;\n    }\n    \n  }\n  \n  &--away {\n\n    #{$bullet-class} {\n      background: radial-gradient(\n                      circle at 3px 3px,\n                      lighten($status-away-color, 35),\n                      $status-away-color\n      );\n    }\n\n    #{$name-class} {\n      color: $status-away-name-color;\n    }\n    \n  }\n  \n  &--dnd {\n\n    #{$bullet-class} {\n      background-color: $status-dnd-color;\n      background: radial-gradient(\n                      circle at 3px 3px,\n                      lighten($status-dnd-color, 30),\n                      $status-dnd-color\n      );\n    }\n    \n    #{$name-class} {\n      color: $status-dnd-name-color;\n    }\n    \n  }\n\n  &--invisible {\n    #{$bullet-class} {\n      background-color: $status-invisible-color;\n      background: radial-gradient(\n                      circle at 3px 3px,\n                      lighten($status-invisible-color, 25),\n                      $status-invisible-color\n      );\n    }\n\n    #{$name-class} {\n      color: $status-invisible-name-color;\n    }\n  }\n  \n  // Eager to chat\n  &--eager {\n    #{$bullet-class} {\n      background: radial-gradient(\n                      circle at 3px 3px,\n                      lighten($status-eager-color, 40),\n                      $status-eager-color\n      );\n    }\n\n    #{$name-class} {\n      color: $status-eager-name-color;\n    }\n    \n  }\n  \n  // Sized\n  &--xs:not(&--named) {\n    @include status-size(\"xs\");\n  }\n\n  &--sm:not(&--named) {\n    @include status-size(\"sm\");\n  }\n\n  &--md:not(&--named) {\n    @include status-size(\"md\");\n  }\n\n  &--lg:not(&--named) {\n    @include status-size(\"lg\");\n  }\n\n  &--fluid {\n    width: $status-fluid-width;\n    height: $status-fluid-width;\n    #{$bullet-class} {\n      width: $status-fluid-width;\n      min-width: $status-fluid-width;\n      height: $status-fluid-width;\n    }\n  }\n  \n  @keyframes #{$prefix}-bubble-anim {\n    0% {\n      transform: scale(1);\n    }\n    20% {\n      transform: scaleY(0.95) scaleX(1.05);\n    }\n    48% {\n      transfor