  background-color: whitesmoke;
  font-family: "Lato", sans-serif;
  transition-timing-function: ease-in-out;
  transition-duration: 0.4s;
}

/* default suggestions for light mode */

.fa-check,
.reviews a,
.star,
.star:after,
.empty-star {
  color: #007185;
}

.reviews a:visited {
  color: #007185;
}

.reviews a:hover {
  color: #a1a1a1;
}

/* dark mode */

.dark {
  background-color: #1a1625;
  background-image: url("https://www.transparenttextures.com/patterns/60-lines.png");
}

.dark div {
  color: white;
}

.dark select,
.dark input {
  background-color: #46424f;
  color: white;
}

.dark .cs-message-list,
.dark .cs-message-input {
  background-color: #2f2b3a;
  color: #46424f;
}

.dark .cs-message__content {
  background-color: #46424f;
}

.dark .swal-text {
  color: black;
}

.dark .cs-typing-indicator {
  background-color: #76737e;
}

.dark .cs-message-input__content-editor-wrapper,
.dark .cs-message-input__content-editor {
  background-color: #5e5a66;
}

.dark .cs-message-input {
  background-color: #2f2b3a;
}

.dark .cs-button {
  color: #908d96;
  opacity: 1;
}

.dark #color-mode-btn:hover,
.dark #customer-service-btn:hover {
  background-color: rgba(255, 255, 255, 0.25);
  text-decoration: none;
}

/* DARK MODE FOR OVERVIEW ELEMENTS */

.dark .reviews a {
  color: #ff6700;
}

.dark .reviews a:hover {
  /* color: #908d96; */
  color: #76737e;
}

.dark .fa-brands {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  box-shadow: 0 0 8px 4px rgba(255, 255, 255, 1);
  background-color: rgba(255, 255, 255, 0.75);
  margin: 0;
}

.dark .fa-facebook {
  box-shadow: 1px 1px 8px 4px rgba(255, 255, 255, 1);
}

.dark .fa-pinterest {
  box-shadow: -1px 0px 8px 4px rgba(255, 255, 255, 1);
}

.dark .star,
.dark .star:after,
.dark .empty-star {
  /* color: #908d96; */
  color: #ff6700;
}

.dark .vertical-line {
  border-color: white;
}

.dark {
  .carousel-container {
    -webkit-mask: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0,
      rgba(0, 0, 0, 1) 8%,
      rgba(0, 0, 0, 1) 92%,
      rgba(0, 0, 0, 0) 100%
    );
            mask: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0,
      rgba(0, 0, 0, 1) 8%,
      rgba(0, 0, 0, 1) 92%,
      rgba(0, 0, 0, 0) 100%
    );
  }

  #image-carousel {
    background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0) 0,
      rgba(0, 0, 0, 0.7) 10%,
      rgba(0, 0, 0, 0.7) 90%,
      rgba(0, 0, 0, 0) 100%
    );
  }
}

/* DARK MODE FOR RELATED ELEMENTS */

#related-carousel-container ::-webkit-scrollbar {
  width: 40px;
}

#related-carousel-container ::-webkit-scrollbar-track {
  background-color: transparent;
}

#related-carousel-container ::-webkit-scrollbar-thumb {
  background-color: rgba(0, 113, 113, 0.5);
  border-radius: 20px;
  border: 2px solid transparent;
  background-clip: content-box;
}

.dark #related-carousel-container ::-webkit-scrollbar-thumb {
  background-color: rgba(255, 103, 0, 0.5);
}

#outfitlist-map-container img {
  max-width: 144px;
  height: auto;
  aspect-ratio: 3 / 2;
  -o-object-fit: cover;
     object-fit: cover;
}

.turn-outfitlist-btn {
  transform: none;
  top: 7rem;
  background-color: rgb(156, 163, 175);
}

#related-carousel-container div div:first-child {
  background-color: rgb(0, 113, 133, 0.25);
}

.dark #related-carousel-container div div:first-child {
  background-color: rgba(255, 103, 0, 0.25);
}

#related-carousel-container div div:first-of-type {
  margin-left: 0;
  padding-bottom: 5px;
}

.dark #outfitlist-button {
  color: black;
}

/* DARK MODE FOR QUESTIONS AND ANSWERS ELEMENTS */

.dark .search-container {
  border: 2px solid white;
}

.dark .search-container,
.dark .search-container input {
  background-color: #1a1625;
}

.dark .search-container input:focus {
  border-bottom: 1px solid white;
}

.dark .search-container i {
  color: white !important;
}

.dark .question-labeler span,
.dark .answer-labeler span {
  background-color: #908d96;
}

.dark #my-images-div img:hover {
  border-color: white;
}

.dark #add-answer-btn:hover,
.dark .expand-answers-btn:hover {
  text-shadow: -0.06ex 0 0 rgba(255, 255, 255, 0.65),
    0.06ex 0 0 rgba(255, 255, 255, 0.65);
}

.dark {
  .ExpandAndAdd-container {
    #expand-questions-btn:hover,
    #add-questions-btn:hover {
      border-color: white;
    }
  }

  .searchProd:hover {
    color: #ff6700;
  }

  #add-questions-btn:hover,
  #expand-questions-btn:hover {
    background-color: rgba(255, 103, 0, 0.5);
  }
}

.dark .answer-container h4 span {
  color: #ff6700;
}

.dark .answer-photo:hover {
  border: 1px solid white;
}

.dark .popup-modal-container {
  background-color: rgba(26, 22, 37, 0.95);
}

.dark .modal {
  background-color: #1a1625;
  background-image: none;
  border-color: white;
}

.dark .modal div:first-child {
  border-bottom-color: white;
}

.dark .modal textarea,
.dark .modal button,
.dark .modal i {
  background-color: #46424f;
  color: white;
}

/* DARK MODE FOR REVIEWS ELEMENTS */

.dark .stars-container h1 {
  /* color: #5e5a66; */
  color: #ff6700;
}

.dark progress::-webkit-progress-bar {
  background-color: #908d96;
}

.dark progress::-webkit-progress-value {
  /* background-color: #5e5a66; */
  background-color: #ff6700;
  box-shadow: 0px 0px 7px white;
}

.dark .reviewResponse {
  background-color: #2f2b3a;
}

.dark #reviewsListButtons button,
.dark .reviewTile button {
  border: 1px solid #46424f;
  background-color: #908d96;
  color: #1a1625;
}

.dark #reviewsListButtons button:hover,
.dark .reviewTile button:hover {
  border: 1px solid #76737e;
}

.dark .productBreakdownIndividual div[name="triangle"] {
  border-top-color: white !important;
}

.dark .review-tile-user {
  color: #ff6700;
}

.dark .fa-check {
  color: #ff6700;
}

.dark .reviewFormSubContainer textarea {
  color: white;
  background-color: #46424f;
}

.dark {
  .productBreakdownIndividual {
    .pointer {
      border-top-color: #ff6700 !important;
    }
  }
}

/* chat animation */
.cs-main-container {
  animation: fadeIn 0.25s ease-in; /* Animation duration and easing */
}

.dark .reviewFormSubContainer {
  background-color: #1a1625;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-out {
  animation: fadeOut 0.25s ease-out forwards;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

/* THE FOLLOWING ARE MEDIA QUERIES FOR NAVBAR */
@media screen and (max-width: 1025px) {
  #nav-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 12px 28px;
    font-size: small;
  }

  #nav-container h1 {
    font-size: 36px;
    text-align: left;
    margin-bottom: 16px;
  }

  #nav-container .flex-1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 16px;
  }

  #nav-container span,
  #nav-container button {
    margin-right: 16px;
  }

  #nav-container input {
    border: 2px solid black;
    border-radius: 1.625rem;
    padding: 8px;
    width: 100%;
  }

  #nav-container .fa-regular,
  #nav-container .fa-solid {
    margin-left: 2px;
  }

  #nav-container .row-start-3,
  #nav-container .row-start-4 {
    width: 100%;
  }
}

@media screen and (max-width: 412px) {
  #nav-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }

  #nav-container h1 {
    font-size: 24px;
    text-align: center;
  }

  #nav-container .flex-1 {
    width: 100%;
  }

  #nav-container span,
  #nav-container button {
    display: block;
    margin: 8px 0;
    text-align: center;
  }

  #nav-container input {
    width: 100%;
    margin: 8px 0;
  }
}

@media screen and (max-width: 575px) {
  #nav-container {
    font-size: 11px;
  }

  #related-carousel-container .container-xl {
    height: 25vh;
    overflow-y: hidden;
  }

  #related-carousel-container .container {
    height: 9rem;
  }

  #related-carousel-container .container-xl div img {
    width: 16rem;
  }

  #related-carousel-container .container-xl div div {
    height: 9rem;
    width: 16rem;
  }
}
